import { customElement } from 'lit/decorators.js';
import { html, LitElement, TemplateResult } from 'lit';

@customElement('zwc-template')
export class ZwcTemplate extends LitElement {
  allText = null;

  // LitElement creates an open shadowRoot and renders inside it

  render(): TemplateResult {
    return html`<slot @slotchange=${this.handleSlotChange}></slot>`;
  }

  // use slotchange event to take action when the assigned nodes change
  // use slot.assignedNodes method to access children assigned to slots in your shadow root
  // flatten - Boolean specifying whether to flatten the assigned nodes by replacing any child <slot> elements with their assigned nodes.
  // Child nodes are sometimes referred to as "light DOM" to distinguish them from the component's shadow DOM.

  protected handleSlotChange(e: Event) {
    if (e) {
      const childNodes = (e.target as HTMLSlotElement).assignedNodes({
        flatten: true,
      });
      const children = childNodes.filter(
        node => node.nodeType !== this.TEXT_NODE,
      ) as HTMLTemplateElement[];
      const [content] = children;
      const data = content.innerHTML.trim();

      // Emit collection;
      this.emitTagContent(data);
    }
  }

  emitTagContent(data: string): void {
    this.dispatchEvent(
      new CustomEvent('change', {
        composed: true,
        cancelable: true,
        bubbles: true,
        detail: data,
      }),
    );
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-template': ZwcTemplate;
  }
}
